import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { StudentService } from '../services/student.service';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-registration-form',
  templateUrl: './registration-form.component.html',
  styleUrls: ['./registration-form.component.css']
})
export class RegistrationFormComponent implements OnInit {
  eCheckReadonly: any;
  private errorMessage: string;
  private successMessage: string;
  courseList = [];

  courseForm = new FormGroup({
    courseID: new FormControl()
  });

  registerForm = new FormGroup({
    firstName: new FormControl(),
    email: new FormControl(),
    lastName: new FormControl(),
    password: new FormControl(),
    id: new FormControl(),
    programOfStudy: new FormControl(),
    designation: new FormControl(),
  });

  constructor(public authService: AuthService, public studentService: StudentService, public router: Router) {

  }

  ngOnInit() {
  }

  addCourse(value) {
    if (!this.courseList.includes(value.courseID) && value.courseID != null) {
      this.courseList.push(value.courseID);
    }
    this.courseForm.reset();
  }

  tryRegister(value) {

    console.log("blah")

    // add courses selected
    value.courseList = this.courseList;

    this.authService.doRegister(value)
      .then(res => {
        this.errorMessage = '';
        this.successMessage = 'Your account has been created';
        sessionStorage.setItem('user', JSON.stringify(value));
      }, err => {
        this.errorMessage = err.message;
        this.successMessage = '';
      });
  }

  goBack() {
    this.router.navigateByUrl("/login")
  }
}
